.jspContainer
{
	overflow: hidden;
	position: relative;
}

.jspPane
{
	position: absolute;
}

.jspVerticalBar
{
//	background: red;
	height: 100%;
	position: absolute;
	right: 0;
	top: 0;
	width: 16px;
}

.jspHorizontalBar
{
	background: red;
	bottom: 0;
	height: 16px;
	left: 0;
	position: absolute;
	width: 100%;
}

.jspCap
{
	display: none;
}

.jspHorizontalBar .jspCap
{
	float: left;
}

.jspTrack
{
//	background: #dde;
	position: relative;
    &:before {
        content: '';
        display: block;
        width: 4px;
        top: 0;
        bottom: 0;
        left: 50%;
        margin-left: -2px;
        background: #fff;
        position: absolute;
        border: 1px solid #d0d0d0;
    }
}

.jspDrag
{
//	background: #bbd;
	cursor: pointer;
	left: 0;
	position: relative;
	top: 0;
    &:after {
        content: '';
        display: block;
        width: 12px;
        height: 12px;
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 50%;
        background: $green;
        transform: translate(-50%,-50%);
    }
}

.jspHorizontalBar .jspTrack,
.jspHorizontalBar .jspDrag
{
	float: left;
	height: 100%;
}

.jspArrow
{
	background: #50506d;
	cursor: pointer;
        direction: ltr;
	display: block;
	margin: 0;
	padding: 0;
	text-indent: -20000px;
}

.jspDisabled
{
	background: #80808d;
	cursor: default;
}

.jspVerticalBar .jspArrow
{
	height: 16px;
}

.jspHorizontalBar .jspArrow
{
	float: left;
	height: 100%;
	width: 16px;
}

.jspVerticalBar .jspArrow:focus
{
        border: 1px solid red;
	outline: none;
}

.jspCorner
{
	background: #eeeef4;
	float: left;
	height: 100%;
}

/* Yuk! CSS Hack for IE6 3 pixel bug :( */
* html .jspCorner
{
	margin: 0 -3px 0 0;
}