section {
    &.top {
        
    }
    &.desk {
        @media (min-width:768px) {
            padding-top: 25px;
            display: block;
        }
        display: flex;
        flex-direction: column;
        
        padding-top: 0;
        background: url(../images/section/desk.webp) no-repeat top center;
        background-size: cover;
        @media (max-width:600px) {
            background-size: auto 100%;
            background-position: bottom left 26%;
        }
        h1 {
            padding: 35px 0 50px 0;
            @media (max-width:767px) {
                padding: 10px 0;
                background: #faf7f4;
            }
            
        }
        .btn {
            font-size: 15px;
        }
        @media (max-width:767px) {
            .bread {
                display: none!important;
            }
        }
        
    }
}
.desk {
    &-btn {
        order: 5;
    }
}