footer {
    background: #ececec;
    padding: 4px 0 16px 0;
    flex: 0 0 auto;
}
.footer {
    &_menu {
        width: 100%;
        padding-top: 3px;
        
        justify-content: center;
        
        @media (max-width:640px) {
            max-width: 435px;
        }
        @media (max-width:767px) {
            padding: 15px 0
        }
        a {
            padding: 0 19px;
            @media (max-width:640px) {
//                width: 50%;   
                padding: 0 10px;
                &:nth-child(3) {
                    padding-left: 0;
                    &:before {
                        display: none!important;
                    }
                }
            }
            @media (max-width:767px) {
                margin: 5px 0
            }
            color: $br;
            font-size: 13px;
            text-decoration: none;
            position: relative; 
            & + a {
                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: -4px;
                    bottom: -4px;
                    width: 1px;
                    background: #a9a9a9;
                }
            }
        }
    }
}
.nebo {
    white-space: nowrap;
    color: $br;
    
    font-size: 12px;
    img {
        margin: 0 5px;
    }
}
.sitemap {
    text-decoration: none;
    font-size: 27px;
    color: #c7b6aa;
}
