.desk {
    padding-top: 50px;
    @media (min-width:768px) {
        justify-content: space-between;
    }
    justify-content: center;
    @media (max-width:499px) {
        margin-bottom: 20px;
        padding-top: 23px;
    }
    &_item {
        
        justify-content: center;
        
        align-items: flex-start;
        
        @media (min-width:768px) {
            width: 25%;
            margin-bottom: 25px;
        }
        @media screen and (max-width:767px) and (min-width:500px) {
            margin-bottom: 10px;
            width: 33%;
        }
        
        width: 50%;
        margin-bottom: 4px;
        
        text-decoration: none;
        text-align: center;
        font-family: $letoB;
        span {
            width: 180px;
            height: 180px;
            @media (max-width:767px) {
                width: 106px;
                height: 106px;
                background: #fff;
            }
            transition: $tr;
            border-radius: 50%;
            &:hover {
                
                background: $white;
                @media (max-width:767px) {
                    background: $green;
                }
                box-shadow: 0 1px 7px rgba(0,0,0,.23);
                i {
                    @media (min-width:768px) {
                        margin-bottom: -19px;
                        margin-top: -19px;
                    }
                    @media (max-width:767px) {
                        color: $white;   
                        filter: hue-rotate(173deg) grayscale(0.75) brightness(1.16) contrast(3) saturate(0) ;
                        
                    }
                }
            }
            
            
            
            font-size: 24px;
            @media (max-width:767px) {
                font-size: 16px;
            }
            color: $dbr;
            
            
            display: flex;
            justify-content: center;
            align-items: center;
                flex-direction: column;
        }
        i {
            display: block;
            height: 90px;
            @media (max-width:767px) {
                height: 31px;   
            }
            width: 100%;
            transition: $tr;
            img {
                @media (max-width:767px) {
                    max-height:31px;
                }
            }
        }
    }
}
.advert {
    justify-content: space-between;
    width: 100%;
    &-right {
        margin-left: -15px;
        margin-right: -15px;
            width: auto;
        .advert {
            &_item {
                width: 33%;   
            }
        }
    }
    
            &_item {
        
        width: 25%;
        
        
        margin-bottom: 25px;
        @media (min-width:501px) {
            padding: 0 10px;
        }
        @media (max-width:767px) {
            width: 50%;
        }
        &-body {
            width: 100%;
            max-width: 250px;
            height: auto;
            padding: 30px 8% 31px 8%;
            
            @media (max-width:500px) {
                padding: 14px 4% 35px 6%;
            }
            @media (max-width:400px) {
                padding: 14px 4% 15px 6%;
            }
            color: #4b4b4b;
            position: relative;
            margin-left: auto;
            margin-right: auto;
            &:hover {
                .advert_item-body-bg_bottom {
                    img {
                        opacity: 0;
                        & + img {
                            opacity: 1;
                        }
                    }
                }
            }
            
            &-bg {
                position: absolute!important;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: 1!important;
                
                display: flex;
                flex-direction: column;
                
                & > * {
//                    background-size: cover;
//                    background-repeat: no-repeat;
//                    background-position: top left;
                    width: 100%;
                    position: relative;
                }
                &_top {
                    flex: 0;
                    //background-image: url(../images/section/advert/top.webp);
                    
                    //min-height: 59px;
                }
                &_center {
                    flex: 1;
                    background: url(../images/section/advert/center.webp) no-repeat top left;
                    background-size: 100% 100%;
                }
                &_bot {
                    position: relative;
                    flex: 0;
//                    background: url(../images/section/advert/bott.webp) no-repeat top left;
                }
                img {
                    width: 100%;
                    display: block;
                    & + img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        opacity: 0;
                    }
                }
                &.green {
                   
                        filter: saturate(0.7) brightness(100%) hue-rotate(46deg);
                        -webkit-filter: saturate(0.7) brightness(100%) hue-rotate(46deg);

                }
                &.blue {
                     
                        filter: saturate(.6) brightness(90%) hue-rotate(156deg);
                        -webkit-filter:saturate(.6) brightness(90%) hue-rotate(156deg);
                   
                }
            }
            & > * {
                position: relative;
                z-index: 2;
            }
        }
        
        &-top {
            position: absolute;
            top: 50px;
            right: -15px;
            
            z-index: 5;
            color: $white;
            @media (max-width:500px) {
                font-size: 8px;
                top: 19px;
                right: 0px;
            }
            & > div {
                margin-bottom: 3px;
                -webkit-clip-path: polygon(100% 0%, 100px 50%, 100% 100%, 15px 100%, 0% 50%, 15px 0%);
                clip-path: polygon(100% 0%, 100px 50%, 100% 100%, 15px 100%, 0% 50%, 15px 0%);
                padding:3px 27px 3px 18px;  
                @media (max-width:500px) {
                    -webkit-clip-path: polygon(100% 0%, 60px 50%, 100% 100%, 15px 100%, 0% 50%, 15px 0%);
                    clip-path: polygon(100% 0%, 60px 50%, 100% 100%, 15px 100%, 0% 50%, 15px 0%);
                    padding: 3px 13px 3px 18px;
                    
                }
            }
            & > .urgently {
                background: $red;
                
            }
        }
        &-name {
            
            margin-bottom: 11px;
            overflow: hidden;
            
            text-decoration: none;
            font-size: 15.27px;
            color: $grey;
            font-family: $letoSB;
            display: block;
            @media (min-width:501px) {
                max-height: 36px;
                min-height: 36px;
            }
            
            @media (max-width:500px) {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 14px;
            }
            &:hover {
                text-decoration: underline;
            }
        }
        &-img {
            position: relative;
            cursor: pointer;
            
            margin-bottom: 8px;
            img {
                width: 100%;
                display: block;
            }
            
            &:hover {
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    background: rgba(255,255,255,0.6);
                }
                 &:after {
                    content: "\e90c";
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    display: flex;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    background: #b8b8b8;

                    color: $white;
                    font-size: 24px;

                    align-items: center;
                    justify-content: center;

                    font-family: 'icomoon' !important;
                      speak: none;
                      font-style: normal;
                      font-weight: normal;
                      font-variant: normal;
                      text-transform: none;
                      line-height: 1;

                      -webkit-font-smoothing: antialiased;
                      -moz-osx-font-smoothing: grayscale;
                }
            }
           
        }
        &-info {
            @media (min-width:501px) {
                margin-bottom: 15px;
            }
            font-family: $got;
            
            white-space: nowrap;
            
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            & > * {
                width: 50%;
                @media (max-width:500px) {
                    order: 3;   
                }
            }
        }
        &-stars {
            font-size: 10px;
            @media (max-width:500px) {
                display: none;
            }
            i {
                color: $grey;
                &.active {
                    color: #f1c40f;
                }
            }
        }
        &-cat {
            font-size: 12.27px;
            color: #9a9a9a;
            @media (max-width:500px) {
                order: 1;   
                font-size: 12px;
            }
        }
        &-aut {
            display: flex;
            justify-content: flex-end;
            
            align-items: center;
            
            font-size: 12.25px;
            margin-top: 15px;
            i {
                color: $lgrey;
                @media (min-width:501px) {
                    margin-right: 5px;
                }
            }
            @media (max-width:500px) {
                order: 2;   
                margin-top: 0px;
                font-size: 10px;
            }
        }
        
        &-cost {
            text-align: right;
            @media (max-width:500px) {
                order: 5;   
                margin-top: 15px;
                width: 100%!important;
                text-align: right;
                font-size: 12px;
            }
        }
    }
        
    &_left {
        &-item {
            display: flex;
            justify-content: space-between;
            border: 1px solid rgba(255,255,255,0);
            padding: 9px 18px 13px 5px;
            &:hover {
                background: $white;
                border-radius: 7px;
                border-color: #ededed;
                box-shadow: 2px 2px 9px rgba(0,0,0,.2)
            }
            &:hover {
                .advert_left-toggle {
                    & > div {
                        display: none;
                        &:last-child {
                            display: block;
                        }
                    }
                }
            
                .owl {
                    &-nav {
                        opacity: 1;
                    }
                }
         
            }
        }
        &-img {
            min-width: 70px;
//            max-width: 70px;
            margin-right: 30px;
            max-width: 135px;
            img {
                display: block;
                width: 100%;
            }
        }
        &-name {
            font-size: 14px;
            font-family: $letoSB;
            color: $dbr;
            text-decoration: none;
            max-height: 36px;
            display: block;
            overflow: hidden;
            
            margin-bottom: 10px;
        }
        &-date,
        &-cat {
            font-size: 12px;
            color: #b9b9b9;
        }
        &-toggle {
            & > div {
                &:last-child {
                    display: none;
                }
            }
        }
        &-date ,
        .advert_item-stars,
        &-cat {
            margin-bottom: 9px;   
            height: 15px;
        }
        &-date {
            i {
                color: $green;
                margin-right: 5px;
            }
        }
        &-comm {
            font-size: 12px;
        }
    }
    &-list {
        .advert {
            &_item {
                width: 100%!important;
                &-body {
                    max-width: 100%;
                    display: flex;
                    justify-content: space-between;
                    &-bg {
                        background-image: url(../images/section/advert/list.webp);
                        background-size: contain;
                        background-repeat: no-repeat;
                        & > * {
                            display: none;
                        }
                    }
                }
                &-img {
                    max-width: 212px;
                }
            }
        }
    }
}

.block {
    width: 100%;
    margin-bottom: 20px;
    &_more {
        font-size: $letoR;
        &-text {
            font-size: 15px;
            line-height: 24px;
            max-height: 50px;
            overflow: hidden;
            
            transition: $tr;
            &.open {
                max-height: 99999px;
            }
        }
        &-a {
            font-size: 14px;
            font-family: $letoT;
            
            color: #361b08;
            
            cursor: pointer;
            display: flex;
            &_right {
                
                justify-content: flex-end;
            }
            span {
                border-bottom: 1px dashed $br;
                transition: $tr;
            }
            &:hover {
                span {
                    border-color: rgba(255,255,255,0);
                }
            } 
            i {
                color: #605449;
                font-size: 8px;
                margin-left: 16px;
            }
        }
    }
    &_ad {
        max-width: 600px;
        margin: 0 auto;
        &-head {
            color: #aaa0a0;
            font-size: 13px;
            font-family: $letoL;
            margin-bottom: 4px;
            letter-spacing: 0.8px;
        }
        &-body {
            max-width: 766px;
            @media (min-width:768px){
                padding: 11px 14px 15px 11px;
                border: 1px solid #d7d7d7; 
            }
            
            
            &__temp > img {
                width: 100%;
                display: block;
            }
        }
        
    }
    &_left {
        width: 270px;
    }
    &_right {
        width: calc( 100% - 300px );
    }
}

.search {
    &_block {
        position: absolute;
        top: 100%;
        width: 100%;
        left: 0;
        right: 0;
        
        
        padding: 11px 0 15px;
        background: #faf7f4;
    }
}

.sort {
    &_top {
        background: $white;
        border-radius: 5px;
        border: 1px solid #bcbcbc;
        padding: 8px 20px;
        color: $dbr;
        
        font-size: 15px;
        
        margin-bottom: 29px;
        a {
            color: $dbr;
            text-decoration: none;
        }
        .jq-selectbox {
            min-width: inherit;
            &__select {
                padding: 11px 26px 0 15px;
            }
            &__trigger {
                width: 17px;
            }
            & li {
                text-align: center;
                padding: 5px 0 6px;
                background-color: unset;
                    color: unset;
                &:hover {
                    text-decoration: underline;
                        background-color: unset;
    color: unset
                }
                &.selected {
                    background-color: unset;
                    color: unset;
                }
            }
        }
        justify-content: space-between;
    }
    &-left {
        a {
            
            color: $dbr;
            font-size: 16px;
            text-decoration: none;
            display: inline-block;
            &.active {
                color: $green;
                font-family: $letoB;
            }
            & + a {
                margin-left: 15px;
            }
        }
    }
}

.aside {
    &_block {
        color: $dbr;
        margin-bottom: 36px;
        
        &-center {
            display: flex;
            justify-content: center;
        }
        &-head {
            font-family: $letoB;
            font-size: 18px;

            padding-bottom: 23px;
            
            display: flex;
            justify-content: space-between;
            width: 100%;
            align-items: center;
        }
        &-body {
            
        }
    }
    
    &-menu {
        a {
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            
            font-size: 16px;
            color: $dbr;
            text-decoration: none;
            & + a {
                margin-top: 20px;
            }
            &:hover {
                color: $green;
                font-family: $letoB;
            }
            &.active {
                font-family: $letoB;
            }
        }
        i {
            transform: rotate(-90deg);
