    &-check {
        label {
            display: block;
        }
    }
}

.range {
    font-size: 14px;
    color: #666666;
    font-family: $letoR;
}

.post {
    &-list {
        
    }
    &_item {
        &-img {
            display: block;
            img {
                width: 100%;
                display: block;
                margin-bottom: 32px;
            }
        }
        &-name {
