.pager {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
//        height: 24px;
        text-decoration: none;
        border: 1px solid rgba(0,0,0,0);
        padding: 7px 5px;
        display: inline-block;
        color: #1c1c1c;
        
        font-size: 15px;
        
        &:hover {
            text-decoration: underline;
        }
        &.active {
            background: #fff;
            border-color: #dadada;
            &:hover {
                text-decoration: none;
            }
        }
        i {
            font-size: 7px;
            color: #999999;
            margin: 0 5px;
            display: inline-block;
        }
        &:first-child {
            i {
                transform: rotate(90deg);
            }
        }
        &:last-child {
            i {
                transform: rotate(-90deg);
            }
        }
    }
}

.repost {
    font-size: 18px;
    font-family: $letoSB;
    
    display: flex;
    align-items: center;
    
    color: #1c1c1c;
    i {
        color: #828282;
        margin-left: 26px;
        font-size: 34px;
    }
}

.tags {
    margin-bottom: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    
    flex-wrap: wrap;
    
    a {
        min-width: 170px;
        border: 2px solid $green;
        display: flex;
        margin: 0 4px 8px 4px;

        padding: 4px 30px 4px 28px;
        
        text-decoration: none;
        color: #000;
        
        align-items: center;
        &:hover {
            background: $green;
            color: #fff;
            i {
                color: #fff;
            }
        }
    }
    i {
        color: $green;
        font-size: 18px;
        
        margin-right: 8px;
    }
}

.comment {
    color: $dbr;
    &-num {
        font-size: 21px;
        font-family: $letoL;
        
        margin-bottom: 40px;
        span {
            font-family: $letoSB;
        }
    }
    &_item {
        padding: 24px 20px;
        display: flex;
        
        &:hover {
            background: #fff;
            
            box-shadow: 2px 2px 9px rgba(0,0,0,.19);
            .rev_hide {
                display: flex!important;
            }
        }
        
        &-left {
            margin-right: 30px;
        }
        &-right {
            color: $dbr;
            width: calc( 100% - 161px );
        }
        &-name {
            font-size: 21px;
            display: flex;
            align-items: center;
            
            margin-bottom: 20px;
            .advert_item-stars {
                font-size: 15px;
                margin-left: 6px;
                margin-top: 5px;
            }
            .status {
                border-radius: 10px;
                padding: 3px 17px;
                margin: 0 7px;
                
                background: #15c1e9;
                
                font-size: 17px;
                color: $white;
            }
        }
        &-text {
            font-size: 15px;
            line-height: 23px;
            font-family: $letoL;
            
            margin-bottom: 15px;
        }
        &-rating {
            i {
                color: #b9b9b9;
                font-size: 13px;
            }
            a {
                text-decoration: none;
                padding: 3px 16px;
                
                background: #ebebeb;
                margin-right: 6px;
                &.like {
                    color: $green;
                }
                &.dis {
                    color: $red;
                    i {
                        transform: rotate(-180deg);
                        display: inline-block;
                    }
                }
            }
        }
    }
    &_add {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        &-item {
            position: relative;
            width: 48.5%;
            
            margin-bottom: 18px;
            
            &.all {
                width: 100%;
            }
            textarea,
            input {
                font-family: $letoR;
                font-size: 17px;
                width: 100%;
                display: block;
                padding: 15px 22px;
                
                border: 1px solid #c4c4c4;
            }
            .btn {
                background: #fff;
                border: 1px solid #d1d1d1;
                
                box-shadow: 3px 3px 6px rgba(77, 77, 77, 0.17);
                color: #6e6e6e;
                margin-bottom: 22px;
                    display: inline-block;
            }
            &.reg {
                color: #4b4b4b;
                font-size: 16px;
                a {
                    color: $green;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
        &-num {
            font-size: 14px;
            color: #949494;
            margin-top: -10px;
            text-align: right;
            span {
                color: $green;
            }
        }
    }
}
.hint {
    position: absolute;
    z-index: 20;
    top: 15px;
    right: 13px;
    
    cursor: pointer;
    i {
        font-size: 22px;
        color: #eaeaea;
    }
    &:hover {
        i {
             color: $green;  
        }
        div {
                display: block;
        }
    }
    div {
        display: none;
        position: absolute;
        top: 100%;
        left: 100%;
        
        padding: 27px 25px;
        
        font-size: 15px;
        color: $dbr;
        line-height: 18px;
        
        background: #fff;
        
        box-shadow: 1px 0 16px #4b3933;
        
        width: 300px;
        border-radius: 10px;
        &.right {
            left: auto;
            right: 100%;
        }
    }
}

.rev {
    margin-bottom: 35px;
    .advert_item-stars {
        font-size: 16px;
    }
    &_hide {
        display: none!important;
    }
    &_block {
        display: flex;
        flex-wrap: wrap;
        padding-top: 15px;
        
        
        justify-content: space-between;
        align-items: center;
        
        .advert_item-stars {
            font-size: 14px;
        }
        .review_line {
            width: 48%;
            div {
                width: 50%;
            }
        }
    }
    &_one {
        display: flex;
        flex-wrap: wrap;
    }
    &_list {
        width: 100%;
        height: 650px;
        overflow: auto;
        .advert_left {
            &-item {
                padding: 0;
                margin-bottom: 10px;
            }
            &-info {
                width: 100%;
            }
            &-img {
                min-width: 120px;
                margin-right: 15px;
            }
            
        }
        .owl {
            &-next,
            &-prev {
                position: absolute;
                top: 0;
                bottom: 0;
                width: 24px;
                background: rgba(0, 0, 0, 0.2)!important;
                z-index: 10;
                left: 0;
                
            }
            &-next {
                left: auto;
                right: 0;
                span {
                    &:before {
                        transform: rotate(-90deg)!important;
                    }
                }
            }
            &-nav {
                opacity: 0;
                span {
                    color: $white;
                    font-size: 0;
                    height: 10px;
                    display: block;
                    &:before {
                        transform: rotate(90deg);
                        font-size: 10px;
                        display: block;
                        content: "\e90f";
                        font-family: 'icomoon' !important;
                        speak: none;
                        font-style: normal;
                        font-weight: normal;
                        font-variant: normal;
                        text-transform: none;
                        line-height: 1;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
            }
        }
        .advert_left-name {
            color: #4b4b4b;
            font-size: 17px;
            font-family: $letoB;
            margin-bottom: 15px;
        }
        .advert_item-stars {
            font-size: 15px;
            margin-bottom: 15px;
        }
    }
    &_type {
        font-size: 13px;
        color: #1c1c1c;
        padding-top: 2px;
    }
}
.review {
    max-width: 670px;
    margin: 0 auto 45px auto;
    &_line {
        display: flex;
        justify-content: space-between;
        
        margin-bottom: 20px;
        div {
            width: 33%;
            font-size: 15px;
        }
        &-color {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-right: 30px;
            
            b {
                background: #fff;
                box-shadow: 0 0 0 1px #dbdbdb inset;
                width: 100%;
                height: 8px;
                max-width: calc( 100% / 5);
            }
            
            span {
                width: 100%;
                height: 8px;
                max-width: calc( 100% / 5 );
                & + span {
                    border-left: 1px solid #9dab7c;
                }
                &:nth-child(1) {
                    background: #ee5e5e;
                }
                &:nth-child(2) {
                    background: #c3972c;
                }
                &:nth-child(3) {
                    background: #7c8328;
                }
                &:nth-child(4) {
                    background: #56a33e;
                }
                &:nth-child(5) {
                    background: #79d24d;
                }
                
            }
        }
    }
}

.user {
    &_info {
        max-width: 400px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        & > div {
            width: 165px;
            
            margin-bottom: 17px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            font-size: 13px;
            color: #1c1c1c;
        }
        a {
            text-decoration: none;
            color: #1c1c1c;
        }
    }
    &_feedback {
        padding-top: 15px;
        margin-bottom: 35px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        
        font-size: 15px;
        line-height: 23px;
        &-left {
            display: flex;
            min-width: 190px;
            align-items: center;
            span {
                font-size: 21px;
                color: #3a3a3a;
            }
            i {
                width: 22px;
                height: 22px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                border:1px solid $green;
                color: #76ad00;
                margin-right: 12px;
                font-size: 11px;
            }
            & + div {
                width: 100%;
                
            }
        }
    }
}

.about {
    background: url(../images/about.webp) no-repeat top center;
    background-size: cover;
    color: $white;
    .bread {
        a {
            color: $white;
        }
    }
    h1 {
        color: $white;
    }
    .h1_i span:after, .h1_i span:before {
        background: $white;
    }
    &_text {
        text-align: center;
        max-width: 960px;
        margin: 0 auto;
        p {
            margin-bottom: 38px;
        }
    }
    h2 {
        margin-bottom: 30px;
    }
    &_icon {
        color: $dbr;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 40px;
        &-left {
            width: 70px;
            min-width: 70px;
            min-height: 70px;
            height: 70px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 3px solid $green;
            
            background: $white;
            margin-right: 30px;
            
            border-radius: 50%;
        }
        &-head {
            font-size: 18px;
            text-transform: uppercase;
            margin-bottom: 22px;
        }
    }
    &_rev {
        background: url(../images/bg_green.webp);
        &_all {
            font-size: 14px;
        }
        h2 {
            margin-bottom: 30px;
        }
        &-item {
            background: $white;
            border: 1px solid #cfcfcf;
            box-shadow: 0px 2px 4px rgba(0,0,0,.1);
            text-align: center;
            
            padding: 25px 30px;
            a {
                color: $dbr;
            }
            .about_rev-item {
                a {
                    display: inline-block;
                }
            }
        }
        &-img {
            img {
                max-width: 133px;
                margin: 0 auto 15px auto;
            }
        }
        &-name {
            font-family: $letoB;
            color: #887364!important;
            text-decoration: none;
        }
        &-info {
            font-size: 13px;
            color: #887364;
            padding: 15px 0;
        }
        &-more {
            color: #241205;
            display: block;
            margin: 15px 0;
            &:hover {
                text-decoration: none;
                
            }
        }
        &-btn {
            a {
                display: inline-block;
                margin: 0 7px;
                font-size: 14px;
                &:hover {
                    text-decoration: none;
                }
                &:not(.btn) {
                    color: $dbr;
                }
                &.btn {
                    background: #fff;
                    color: $dbr;
                    i {
                        color: $green;
                        margin-right: 7px;
                    }
                }
            }
        }
    }
    &_how {
        color: $dbr;
        h2 {
            margin-bottom: 30px;
        }
        &-block {
            display: flex;
            justify-content: center;
            align-items: center;
            
            margin-bottom: 97px;
            i {
                font-size: 40px;
                margin: 40px;
                display: block;
                color: $green;
            }
            & > div {
                width: 250px;
                height: 118px;
                background-image:  url(../images/section/about_how/3.webp);
                background-position: top left;
                background-repeat: no-repeat;
                background-size: contain;
                &:last-child {
                    width: 118px;
                }
                &.about_how-start {
                    background: url(../images/section/about_how/2.webp) no-repeat ;
                    & ~ div {
                        background: url(../images/section/about_how/1.webp) no-repeat ;
                    }
                }
            }
        }
        &-head {
            font-size: 21px;
            text-align: center;
            margin-bottom: 85px;
        }
    }
}
