@import "variables";
@import "mixins";
html,
body {
    height: 100%;
    background: url(../images/bg.webp) #f8f8f8;
    display: flex;
    flex-direction: column;
    font-family: $letoR;
}
.main {
    flex: 1 0 auto;
    padding-top: 57px;
}

section {
    padding: 25px 0;
    position: relative;
}
p {
    font-size: 15px;
    line-height: 23px;
}
.mb-30 {
    margin-bottom: 30px;
}
h1 {
    color: $dbr;
    font-size: 40px;
    text-align: center;
    display: block;
    width: 100%;
    
    font-family: $letoB;
    
    @media (max-width:767px) {
        font-size: 17px;
    }
}
h2 {
    font-size: 32px;
    font-family: $letoSB;
    display: block;
    width: 100%;
    text-align: center;
    
    color: $dbr;
}
.h1 {
    &_i {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        i {
            display: block;
            font-size: 32px;
            
            //position: relative;
            //color: $dbr;
            
        }
        span {
            margin: 13px 0 30px 0;
            position: relative;
            &:after,
            &:before {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                min-width: 200px;
                height: 2px;
                background: $dbr;
                transform:  translate(0,-50%);
            }
            &:before { 
                left: calc( 100% + 20px );
            }
            &:after { 
                right: calc( 100% + 20px );
            }
            
        }
    }
}
.h2 {
    margin: 22px 0 35px 0;
    &_btn {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        white-space: nowrap;
        & > * {
            flex: 1;
            padding-left: 10px;
            padding-right: 10px;
        }
        &-block {
            display: flex;
            justify-content: flex-end;
            align-items: center;
        }
        .btn {
//            position: absolute;
//            right: 15px;
//            top: 50%;
//            transform: translate(0,-50%);
        }
    }
    &_min {
        font-size: 21px;
        color: #3a3a3a;
        line-height: 30px;
    }
}

@media (max-width: 991px) {
    .container {
        max-width: 100%!important;
    }
}

