header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: $green;
    z-index: 99;
    padding: 10px 0;
    font-family: $letoSB;
    a {
        color: #fff;
        text-decoration: none;
        transition: color ease 0.4s;
        i {
            font-size: 26px;
        }
        &:hover {
            color: $gold;
        }
    }
}
nav {
    
    @media (max-width: 991px) { 
        position: fixed!important;
        top:0;
        left: -11000%;
        right: 11000%;
        bottom: 0;
        
    }
    a {
        margin: 0 13px;
        font-size: 15px;
        white-space: nowrap;
    }
}
.logo {
    
    img {
        width: 100%;
        max-width: 170px;
    }
}
.header {
    &_control {
        a {
            padding: 4px 15px;  
            & + a {
                border-left: 1px solid $white;
            }
        }
    }
}
.search {
    &_block {
        display: none;
        @media (min-width:768px) {
            position: absolute;
            top: 100%;
            width: 100%;
            left: 0;
            right: 0;
        }
        
        
        
        padding: 11px 0 15px;
        background: #faf7f4;
    }
    &_res {
        font-size: 18px;
        color: $dbr;
        
        margin-bottom: 30px;
        a {
            color: $green;
            display: inline-block;
            padding: 0 10px;
            
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_input {
        display: block;
        width: 100%;
        padding: 18px 20px;
        background: $white;
        border: 1px solid #dadada;
        box-shadow:inset 0 0 6px rgba(0,0,0,.15);
        position: relative;
        z-index: 1;
        
        color: $dbr;
        &-block {
           position: relative;
        }
        &-button {
            background: none;
            border: none;
            font-size: 20px;
            color: #bfbfbf;
            position: absolute;
            z-index: 2;
            top: 50%;
            right: 10px;
            transform: translate(-50%,-50%);
        }
    }
    
}