.bread {
    margin-bottom: 30px;
    a {
        color: $dbr;
        
        font-size: 14px;
        text-decoration: none;
//        &:first-child {
//            text-decoration: none;
//            &:hover {
//                text-decoration: underline;
//            }
//        }
        //text-decoration: underline;
        &:hover {
            text-decoration: underline;
        }
        & + a {
            &:before {
                content: '/';
                display: inline-block;
                padding: 0 3px;
            }
        }
    }
}