.btn {
    border-radius: 35px;
    padding: 7px 29px;
    font-size: 14px;
    color: $white;
    text-decoration: none;
    border: 2px solid $white;
    transition: $tr;
    
    white-space: nowrap;
    
    font-family: $letoSB;
    @media (max-width:499px) {
        border-radius: 10px;
        width: calc( 100% - 20px);
        margin-left: 10px;
        margin-right: 10px;
        text-align: center;
    }
    &:hover {
        border-color: $gold;
        color: $gold;
    }
    &_yellow {
        background: $yellow;
        color: $dbr;
        border-color: $yellow;
        box-shadow: 0 4px 6px rgba(0,0,0,.3);
        &:hover {
            border-color: $yellow;
            background: $white;
            color: $dbr;
        }
    }
    &_top {
        
    }
    &_more {
        display: block;
        text-align: center;
        color: $dbr;
        background: $white;
        box-shadow: 3px 3px 6px rgba(77, 77, 77, 0.2); 
        font-family: $letoL;
        border-radius: 10px;
        font-size: 16px;
        i {
            color: #999999;
            font-size: 8px;
            margin-left: 15px;
        }
    }
    &_grey {
        color: $dgrey;
        
        background: #f8f8f8;
        border-color: #f8f8f8;
        
        box-shadow: 2px 2px 8px rgba(0,0,0,.3);
        &:hover {
            border-color: $green;
            color: $green;
        }
    }
    &_add {
        background: $green;
        border-color: $green;
        display: flex;
            align-items: center;
            justify-content: center;
        &:hover {
            color: $white;
        }
        i {
            width: 21px;
            height: 21px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            background: #fff;
            color: $green;
            margin-right: 12px; 
            font-size: 11px;
        }
    }
}